img{
    overflow-x:hidden;
    height: 100vh;
    width: 100%;  
    display:block; 
}

.home-page-div {
    color: white;
    overflow: hidden;
    height: 93vh;
    background: linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
    ), url('../resources/MangroveHome.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 50px;
}

.home-page-title {
    margin-top: auto;
    font-size: 5rem;
    margin-bottom: -60px;
}

.home-page-introduction {
    font-size: 2rem;
}

.button-link {
    display: inline-block;
    padding: 10px 20px;
    margin: 10px;
    background-color: transparent;
    color: white;
    text-align: center;
    text-decoration: none;
    border-radius: 10px;
    transition: background-color 0.3s;
    width: fit-content;
    border-color: white;
    border-style:solid;
    border-width: 1px;
    margin-bottom: 150px;
    font-size: 2rem;
}

.button-link:hover, .button-link:focus {
    background-color: white;
    color: black;
    text-decoration: none;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .title-on-mobile {
        font-size: 50px;
        margin-bottom: 60px;
    }

    .introduction-on-mobile {
        font-size: 24px;
        margin-bottom: 20px;
    }

    .home-page-title-on-mobile {
        margin-top: 40px;
    }

    .home-page-div-on-mobile {
        padding-left: 30px;
    }
  }